<template>
  <v-card>
    <v-container fluid grid-list-lg>
      <v-list>
        <v-subheader inset>Question List</v-subheader>

        <v-list-tile
          v-for="question in questions"
          :key="question.id"
          avatar
          @click="onSelect(question)"
          ripple
        >
          <v-list-tile-avatar>
            <v-icon
              :class="[question.suggestion?'yellow white--text':'red white--text']"
            >{{ question.suggestion?'highlight':'note_add' }}</v-icon>
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title v-html="question.title"></v-list-tile-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-btn icon @click="onRemove(question)">
              <v-icon color="red lighten-1">remove_circle</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-container>
    <DeleteDialog :data="dialog"/>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import DeleteDialog from "./DeleteDialog";
export default {
  data() {
    return {
      dialog: { show: false, id: null }
    };
  },
  computed: {
    ...mapGetters(["questions"])
  },
  methods: {
    ...mapActions(["selectQuestion"]),
    onSelect(question) {
      this.selectQuestion(question);
    },
    onRemove(id) {
      this.dialog.show = true;
      this.dialog.id = id;
    }
  },
  components: {
    DeleteDialog
  }
};
</script>

