import Vue from 'vue';
import Vuex from 'vuex';
import uniqid from 'uniqid';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    uid: null,
    questions: [],
    selectedQuestion: null,
    newQuestion: false,
    uploading: false
  },
  getters: {
    questions: state => {
      return clone(state.questions);
    },
    selectedQuestion: state => {
      return clone(state.selectedQuestion);
    },
    newQuestion: state => {
      return state.newQuestion;
    },
    uploading: state => {
      return state.uploading;
    }
  },
  mutations: {
    setNewQuestion: (state, bool) => {
      state.newQuestion = bool;
    },
    setSelectedQuestion: (state, id) => {
      state.selectedQuestion = id;
    },
    addQuestion: (state, q) => {
      state.questions.push(q);
    },
    updateQuestion: (state, q) => {
      let idx = state.questions.findIndex(i => i.id === q.id);
      state.questions[idx] = q;
      state.questions = clone(state.questions);
    },
    deleteQuestion: (state, question) => {
      state.questions = state.questions.filter(i => i.id !== question.id);
    },
    deleteAllQuestions: state => {
      state.questions = [];
    },
    selectQuestion: (state, question) => {
      state.selectedQuestion = question;
    },
    setUploading: (state, bool) => {
      state.uploading = bool;
    },
    initApp: (state, { uid, data }) => {
      state.uid = uid;
      state.questions = data;
    }
  },
  actions: {
    saveQuestion: (context, q) => {
      // update
      if (q.id) {
        context.commit('updateQuestion', q);
      } else {
        const question = { id: generateId(), ...q };
        context.commit('addQuestion', question);
      }
      context.dispatch('cancelEdit');
      saveData(context);
    },
    deleteQuestion: (context, question) => {
      if (!question) {
        context.commit('deleteAllQuestions');
      } else {
        context.commit('deleteQuestion', question);
      }
      context.dispatch('cancelEdit');
      saveData(context);
    },
    createQuestion: context => {
      context.commit('setNewQuestion', true);
    },
    cancelEdit: context => {
      context.commit('setSelectedQuestion', null);
      context.commit('setNewQuestion', false);
    },
    selectQuestion: (context, question) => {
      context.commit('selectQuestion', question);
    },
    uploadData: context => {
      context.commit('setUploading', true);
      const date = new Date();
      const time = `${date.toLocaleDateString()}-${date.toLocaleTimeString()}`;
      const questions = {
        time,
        data: context.state.questions.filter(i => !i.suggestion)
      };
      const suggestions = {
        time,
        data: context.state.questions.filter(i => i.suggestion)
      };

      const calls = [];
      calls.push(axios.post(`/questions/${context.state.uid}.json`, questions));
      calls.push(
        axios.post(`/suggestions/${context.state.uid}.json`, suggestions)
      );

      axios.all(calls).then(res => {
        context.commit('setUploading', false);
        context.dispatch('deleteQuestion', null);
      });
    },
    loadData: context => {
      let uid = localStorage.getItem('uid');
      if (!uid) {
        uid = generateId('user-');
        localStorage.setItem('uid', uid);
      }
      let data = localStorage.getItem('data');
      if (data) {
        data = JSON.parse(data);
      } else {
        data = [];
      }
      context.commit('initApp', { uid, data });
    }
  }
});

function saveData(context) {
  localStorage.setItem('data', JSON.stringify(context.state.questions));
}

function generateId(prefix) {
  return uniqid(prefix);
}

function clone(data) {
  return JSON.parse(JSON.stringify(data));
}
