<template>
  <v-card>
    <v-container fluid grid-list-lg>
      <v-form ref="form" @submit.prevent="onSubmit">
        <v-text-field clearable v-model="form.title" color="primary" label="Title" required></v-text-field>
        <v-divider></v-divider>
        <v-textarea
          clearable
          v-model="form.content"
          auto-grow
          color="primary"
          label="Content"
          rows="5"
        ></v-textarea>
        <v-checkbox label="Suggestion" v-model="form.suggestion"></v-checkbox>
        <v-card-actions>
          <v-btn @click="cancelEdit">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" type="submit">Save</v-btn>
        </v-card-actions>
      </v-form>
    </v-container>
  </v-card>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    form() {
      if (this.$store.getters.selectedQuestion) {
        return this.$store.getters.selectedQuestion;
      }
      return {
        title: "",
        content: "",
        suggestion: false
      };
    }
  },
  methods: {
    ...mapActions(["saveQuestion", "cancelEdit"]),
    onSubmit() {
      this.saveQuestion(this.form);
    }
  }
};
</script>

