<template>
  <v-dialog v-model="data.show" max-width="290">
    <v-card>
      <v-card-title class="headline">Are you sure you want to delete?</v-card-title>

      <!-- <v-card-text>This action cannot be undo!</v-card-text> -->
      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn color="error" flat="flat" @click="data.show = false">Cancel</v-btn>

        <v-btn color="primary text--white" @click="onDelete">Yes</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["data"],
  methods: {
    ...mapActions(["deleteQuestion"]),
    onDelete() {
      this.deleteQuestion(this.data.id);
      this.data.show = false;
    }
  }
};
</script>

