<template>
  <v-app>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span>Q-A</span>
        <v-icon color="primary" class="white--text" right light>question_answer</v-icon>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn color="error" class="white--text" @click="dialog.show = true">Clear
        <v-icon right light>delete</v-icon>
      </v-btn>
      <v-btn
        :loading="uploading"
        :disabled="uploading"
        color="primary"
        class="white--text"
        @click="uploadData"
      >Upload
        <v-icon right light>cloud_upload</v-icon>
      </v-btn>
    </v-toolbar>

    <v-content>
      <Questions/>
    </v-content>
    <DeleteDialog :data="dialog"/>
  </v-app>
</template>

<script>
import Questions from "./views/Questions";
import DeleteDialog from "./components/DeleteDialog";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "App",
  components: {
    Questions,
    DeleteDialog
  },
  data() {
    return {
      dialog: {
        show: false,
        id: null
      }
    };
  },
  computed: {
    ...mapGetters(["uploading"])
  },
  methods: {
    ...mapActions(["uploadData", "loadData"])
  },
  created() {
    this.loadData();
  }
};
</script>
<style lang="scss">
* {
  box-sizing: border-box;
}
</style>
